import { User } from "firebase/auth";
import { getDatabase, ref, remove, set, update } from "firebase/database";
import { ShowType } from "../api";

export const writeUserData = (email: any, uid: any) => {
  const db = getDatabase();
  set(ref(db, "users/" + uid), {
    email: email,
  });
  console.log("User data written!");
  console.log("User ID: " + uid);
  console.log("User email: " + email);
};

export const handleUserFavorite = (show: ShowType, user: User) => {
  const db = getDatabase();
  update(ref(db, "users/" + user.uid + "/favorites"), { [show.id]: show });
};

export const removeShow = (show: ShowType, user: User) => {
  const db = getDatabase();
  remove(ref(db, "users/" + user.uid + "favorites" + show.id));
};
